}

.header_home {
  background-color: var(--main-color);
  padding: 22px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-top-left-radius: inherit;
  border-bottom-left-radius: inherit;
  transition: 0.3s;
}

.header_home:hover {
  background-color: #2196f3;
}

.header_home_active {
  background-color: #2196f3;
  padding: 22px;
  display: -webkit-flex;
